class UMLGenerator {
  constructor(designTool) {
    this.designTool = designTool;
  }

  generateUML() {
    let umlOutput = "@startuml\n\n";

    // Process nodes (classes and interfaces)
    this.designTool.nodes.forEach((node) => {
      if (node.nodeType === "class") {
        umlOutput += this.processClass(node);
      } else if (node.nodeType === "interface") {
        umlOutput += this.processInterface(node);
      }
    });

    // Process relationships
    this.designTool.relations.forEach((relation) => {
      umlOutput += this.processRelation(relation);
    });

    umlOutput += "\n@enduml";
    return umlOutput;
  }

  processClass(node) {
    let classUML = `class ${node.name} {\n`;
    node.properties.forEach((prop) => {
      classUML += `  **${prop}**\n`; // Bold properties
    });
    node.methods.forEach((method) => {
      classUML += `  **${method}**\n`; // Bold methods
    });
    classUML += "}\n\n";
    return classUML;
  }

  processInterface(node) {
    let interfaceUML = `interface ${node.name} {\n`;
    node.methods.forEach((method) => {
      interfaceUML += `  **${method}**\n`; // Bold methods
    });
    interfaceUML += "}\n\n";
    return interfaceUML;
  }

  processRelation(relation) {
    const arrowType = this.getArrowType(relation.type);
    return `${relation.startNode.name} ${arrowType} ${relation.endNode.name}\n`;
  }

  getArrowType(relationType) {
    switch (relationType) {
      case "extends":
        return "<|--";
      case "implements":
        return "<|..";
      case "uses":
        return "..>";
      default:
        return "--";
    }
  }
}

export default UMLGenerator;
