import jwLogo from "./assets/jwlogo.png";
import UMLGenerator from "./umlGenerator.js";

// Then, set the src of the image programmatically
document.addEventListener("DOMContentLoaded", () => {
  const logoImg = document.getElementById("jwlogo");
  if (logoImg) {
    logoImg.src = jwLogo;
  }
});

let designToolInstance = null;

// Ensure the DOM is fully loaded before initializing the DesignTool
document.addEventListener("DOMContentLoaded", () => {
  console.log("DOM fully loaded and parsed");

  const canvas = document.getElementById("designCanvas");

  if (canvas) {
    console.log("Canvas element found");

    // Initialize the DesignTool only if not already initialized
    if (!designToolInstance) {
      designToolInstance = new DesignTool(canvas);
      console.log("DesignTool initialized and ready to use");

      // Add event listeners for buttons
      document.getElementById("addClassBtn").addEventListener("click", () => {
        designToolInstance.addClass("New Class");
      });

      document
        .getElementById("addInterfaceBtn")
        .addEventListener("click", () => {
          designToolInstance.addInterface("New Interface");
        });

      document
        .getElementById("addRelationBtn")
        .addEventListener("click", () => {
          designToolInstance.onAddRelationClick();
        });

      const generateUMLBtn = document.getElementById("generateUMLBtn");
      if (generateUMLBtn) {
        Object.assign(generateUMLBtn.style, {
          backgroundColor: "#3498db",
          color: "white",
          border: "none",
          padding: "10px 15px",
          margin: "5px",
          borderRadius: "5px",
          cursor: "pointer",
          fontSize: "14px",
        });
        generateUMLBtn.addEventListener("mouseover", () => {
          generateUMLBtn.style.backgroundColor = "#2980b9";
        });
        generateUMLBtn.addEventListener("mouseout", () => {
          generateUMLBtn.style.backgroundColor = "#3498db";
        });
        generateUMLBtn.addEventListener("click", () => {
          designToolInstance.showUML();
        });
      } else {
        console.error("Generate UML button not found");
      }

      // Add a sample class node to verify drawing
      designToolInstance.addClass("Test Node");
    } else {
      console.log("DesignTool already initialized");
    }
  } else {
    console.error("Canvas element not found!");
  }

  const modal = document.getElementById("solidModal");
  const closeBtn = document.querySelector("#solidModal .close");
  const toggleSOLIDBtn = document.getElementById("toggleSOLIDBtn");

  if (!modal) {
    console.error("Modal element not found!");
  }

  if (!closeBtn) {
    console.error("Close button not found!");
  }

  if (!toggleSOLIDBtn) {
    console.error("Toggle SOLID button not found!");
  }

  if (toggleSOLIDBtn && modal) {
    toggleSOLIDBtn.addEventListener("click", () => {
      modal.style.display = "block";
    });
  }

  if (closeBtn && modal) {
    closeBtn.addEventListener("click", () => {
      modal.style.display = "none";
    });
  }

  if (modal) {
    window.addEventListener("click", (event) => {
      if (event.target == modal) {
        modal.style.display = "none";
      }
    });
  }
});

let nodeIdCounter = 1; // Global counter for generating unique IDs

class ClassNode {
  constructor(x, y, name) {
    this.x = x;
    this.y = y;
    this.width = 200; // Minimum width
    this.height = 100; // Minimum height
    this.name = name;
    this.properties = [];
    this.methods = [];
    this.cornerRadius = 10;
    this.padding = 10;
    this.lineHeight = 20;
    this.nodeType = "class";
    this.backgroundColor = "#f0f9ff"; // Light blue background
    this.textColor = "#2c3e50"; // Dark blue text
    this.fontSize = 14;
    this.fontFamily = "Arial";
    this.propertyFont = `${this.fontSize}px ${this.fontFamily}`;
    this.methodFont = `italic ${this.fontSize}px ${this.fontFamily}`;
    this.buttonSize = 16; // Size of the P and M buttons
    this.buttonPadding = 5; // Padding between buttons
  }

  draw(ctx) {
    this.updateDimensions(ctx);

    // Background and border
    ctx.fillStyle = this.backgroundColor;
    ctx.strokeStyle = "#3498db";
    ctx.lineWidth = 2;
    this.roundRect(
      ctx,
      this.x,
      this.y,
      this.width,
      this.height,
      this.cornerRadius
    );
    ctx.fill();
    ctx.stroke();

    // <<class>> tag
    ctx.font = `italic ${this.fontSize}px ${this.fontFamily}`;
    ctx.fillStyle = "rgba(127, 140, 141, 0.7)";
    ctx.textAlign = "center";
    ctx.textBaseline = "top";
    ctx.fillText(`<<${this.nodeType}>>`, this.x + this.width / 2, this.y + 10);

    // Separator line
    const separatorY = this.y + 40;
    ctx.beginPath();
    ctx.moveTo(this.x, separatorY);
    ctx.lineTo(this.x + this.width, separatorY);
    ctx.strokeStyle = "#3498db";
    ctx.stroke();

    // Class name
    ctx.fillStyle = this.textColor;
    ctx.font = `bold ${this.fontSize + 2}px ${this.fontFamily}`;
    ctx.textAlign = "center";
    ctx.textBaseline = "top";
    ctx.fillText(this.name, this.x + this.width / 2, separatorY + 10);

    // Properties and methods
    let yOffset = separatorY + 40; // Adjusted to provide more space
    this.properties.forEach((prop) => {
      yOffset = this.drawItem(
        ctx,
        prop,
        this.x + this.padding,
        yOffset,
        "property"
      );
    });
    this.methods.forEach((method) => {
      yOffset = this.drawItem(
        ctx,
        method,
        this.x + this.padding,
        yOffset,
        "method"
      );
    });

    // 'P' and 'M' buttons positioned at the bottom-right corner of the node
    const buttonY = this.y + this.height - this.buttonSize - this.buttonPadding;
    this.drawAddButton(
      ctx,
      this.x + this.width - this.buttonSize * 2 - this.buttonPadding,
      buttonY,
      "P"
    );
    this.drawAddButton(
      ctx,
      this.x + this.width - this.buttonSize - this.buttonPadding,
      buttonY,
      "M"
    );

    // Node delete button
    this.drawDeleteButton(ctx, this.x + this.width - 20, this.y + 20);
  }

  drawItem(ctx, text, x, y, type) {
    const maxWidth = this.width - this.padding * 2 - 30;
    const lines = this.wrapText(ctx, text, maxWidth);

    ctx.fillStyle = this.textColor;
    ctx.textAlign = "left";
    ctx.textBaseline = "top";

    // Set font based on item type
    ctx.font = type === "method" ? this.methodFont : this.propertyFont;

    lines.forEach((line, i) => {
      ctx.fillText(line, x, y + i * this.lineHeight);
    });

    // Draw delete button
    const deleteX = this.x + this.width - 20;
    const deleteY =
      y + (lines.length * this.lineHeight) / 2 - this.lineHeight / 2;
    this.drawDeleteButton(ctx, deleteX, deleteY);

    return y + lines.length * this.lineHeight + 5; // Return new yOffset
  }

  updateDimensions(ctx) {
    ctx.font = this.propertyFont;
    let maxWidth = 200; // Minimum width
    let totalHeight = 40 + 10 + 30; // Separator line + space + class name height

    // Calculate width based on content
    [this.name, ...this.properties, ...this.methods].forEach((item) => {
      const itemWidth = ctx.measureText(item).width + this.padding * 2;
      maxWidth = Math.max(maxWidth, itemWidth);
    });

    // Calculate height based on content
    const contentWidth = maxWidth - this.padding * 2 - 30;
    this.properties.forEach((prop) => {
      const lines = this.wrapText(ctx, prop, contentWidth);
      totalHeight += lines.length * this.lineHeight + 5;
    });
    this.methods.forEach((method) => {
      const lines = this.wrapText(ctx, method, contentWidth);
      totalHeight += lines.length * this.lineHeight + 5;
    });

    totalHeight += 40; // Add extra space for 'P' and 'M' buttons

    this.width = maxWidth + 60; // Add extra space for buttons
    this.height = Math.max(100, totalHeight + this.padding * 2);
  }

  wrapText(ctx, text, maxWidth) {
    const words = text.split(" ");
    const lines = [];
    let currentLine = "";

    for (let i = 0; i < words.length; i++) {
      const word = words[i];
      const width = ctx.measureText(currentLine + word).width;
      if (width < maxWidth) {
        currentLine += (currentLine ? " " : "") + word;
      } else {
        lines.push(currentLine);
        currentLine = word;
      }
    }
    lines.push(currentLine);
    return lines;
  }

  roundRect(ctx, x, y, width, height, radius) {
    if (width < 2 * radius) radius = width / 2;
    if (height < 2 * radius) radius = height / 2;
    ctx.beginPath();
    ctx.moveTo(x + radius, y);
    ctx.arcTo(x + width, y, x + width, y + height, radius);
    ctx.arcTo(x + width, y + height, x, y + height, radius);
    ctx.arcTo(x, y + height, x, y, radius);
    ctx.arcTo(x, y, x + width, y, radius);
    ctx.closePath();
  }

  drawAddButton(ctx, x, y, label) {
    ctx.fillStyle = "#3498db";
    ctx.fillRect(x, y, this.buttonSize, this.buttonSize);
    ctx.fillStyle = "white";
    ctx.font = "bold 12px Arial";
    ctx.textAlign = "center";
    ctx.textBaseline = "middle";
    ctx.fillText(label, x + this.buttonSize / 2, y + this.buttonSize / 2);
  }

  drawDeleteButton(ctx, x, y) {
    const radius = 8;
    ctx.fillStyle = "rgba(231, 76, 60, 0.8)"; // Semi-transparent red
    ctx.beginPath();
    ctx.arc(x, y, radius, 0, Math.PI * 2);
    ctx.fill();

    ctx.fillStyle = "white";
    ctx.font = "bold 12px Arial";
    ctx.textAlign = "center";
    ctx.textBaseline = "middle";
    ctx.fillText("×", x, y);
  }

  isDeleteButtonClicked(x, y) {
    const buttonX = this.x + this.width - 20;
    const buttonY = this.y + 20;
    return this.isPointInsideCircle(x, y, buttonX, buttonY, 8);
  }

  isAddPropertyButtonClicked(x, y) {
    const buttonX =
      this.x + this.width - this.buttonSize * 2 - this.buttonPadding;
    const buttonY = this.y + this.height - this.buttonSize - this.buttonPadding;
    return this.isPointInsideRect(
      x,
      y,
      buttonX,
      buttonY,
      this.buttonSize,
      this.buttonSize
    );
  }

  isAddMethodButtonClicked(x, y) {
    const buttonX = this.x + this.width - this.buttonSize - this.buttonPadding;
    const buttonY = this.y + this.height - this.buttonSize - this.buttonPadding;
    return this.isPointInsideRect(
      x,
      y,
      buttonX,
      buttonY,
      this.buttonSize,
      this.buttonSize
    );
  }

  isPointInsideCircle(x, y, cx, cy, radius) {
    const dx = x - cx;
    const dy = y - cy;
    return dx * dx + dy * dy <= radius * radius;
  }

  isPointInsideRect(x, y, rectX, rectY, rectWidth, rectHeight) {
    return (
      x >= rectX &&
      x <= rectX + rectWidth &&
      y >= rectY &&
      y <= rectY + rectHeight
    );
  }

  isPointInside(x, y) {
    return (
      x >= this.x &&
      x <= this.x + this.width &&
      y >= this.y &&
      y <= this.y + this.height
    );
  }

  addProperty(property) {
    if (property && property.trim() !== "") {
      this.properties.push(property);
      this.updateDimensions(
        document.getElementById("designCanvas").getContext("2d")
      );
    }
  }

  addMethod(method) {
    if (method && method.trim() !== "") {
      this.methods.push(method);
      this.updateDimensions(
        document.getElementById("designCanvas").getContext("2d")
      );
    }
  }

  checkDeleteButtons(x, y) {
    const ctx = document.createElement("canvas").getContext("2d");
    ctx.font = this.propertyFont;
    let yOffset = this.y + 40 + 10 + 30; // Start after the class name

    // Handle properties
    for (let i = 0; i < this.properties.length; i++) {
      const prop = this.properties[i];
      const maxWidth = this.width - this.padding * 2 - 30;
      const lines = this.wrapText(ctx, prop, maxWidth);
      const deleteX = this.x + this.width - 20;
      const deleteY =
        yOffset + (lines.length * this.lineHeight) / 2 - this.lineHeight / 2;
      if (this.isPointInsideCircle(x, y, deleteX, deleteY, 8)) {
        return { type: "property", index: i };
      }
      yOffset += lines.length * this.lineHeight + 5;
    }

    // Handle methods
    for (let i = 0; i < this.methods.length; i++) {
      const method = this.methods[i];
      const maxWidth = this.width - this.padding * 2 - 30;
      const lines = this.wrapText(ctx, method, maxWidth);
      const deleteX = this.x + this.width - 20;
      const deleteY =
        yOffset + (lines.length * this.lineHeight) / 2 - this.lineHeight / 2;
      if (this.isPointInsideCircle(x, y, deleteX, deleteY, 8)) {
        return { type: "method", index: i };
      }
      yOffset += lines.length * this.lineHeight + 5;
    }

    return null;
  }

  // ... other methods ...
}

class InterfaceNode extends ClassNode {
  constructor(x, y, name) {
    super(x, y, name);
    this.nodeType = "interface";
    console.log(`InterfaceNode created: ${this.name}`);
  }

  editName(newName) {
    this.name = newName;
    console.log(`Interface name changed to: ${newName}`);
  }
}

class Relation {
  constructor(startNode, endNode, type) {
    this.startNode = startNode;
    this.endNode = endNode;
    this.type = type;
  }

  draw(ctx) {
    const startX = this.startNode.x + this.startNode.width / 2;
    const startY = this.startNode.y + this.startNode.height / 2;
    const endX = this.endNode.x + this.endNode.width / 2;
    const endY = this.endNode.y + this.endNode.height / 2;

    // Draw the line
    ctx.beginPath();
    ctx.moveTo(startX, startY);
    ctx.lineTo(endX, endY);
    ctx.strokeStyle = "#000";
    ctx.stroke();

    // Draw the label
    const midX = (startX + endX) / 2;
    const midY = (startY + endY) / 2;
    ctx.font = "12px Arial";
    ctx.fillStyle = "#000";
    ctx.textAlign = "center";
    ctx.textBaseline = "middle";
    ctx.fillText(this.type, midX, midY - 10);

    // Draw arrowhead
    const angle = Math.atan2(endY - startY, endX - startX);
    ctx.beginPath();
    ctx.moveTo(endX, endY);
    ctx.lineTo(
      endX - 10 * Math.cos(angle - Math.PI / 6),
      endY - 10 * Math.sin(angle - Math.PI / 6)
    );
    ctx.lineTo(
      endX - 10 * Math.cos(angle + Math.PI / 6),
      endY - 10 * Math.sin(angle + Math.PI / 6)
    );
    ctx.closePath();
    ctx.fill();
  }

  isConnectedToNode(node) {
    return this.startNode === node || this.endNode === node;
  }
}

class DesignTool {
  constructor(canvas) {
    console.log("Initializing DesignTool...");
    this.canvas = canvas;
    this.ctx = canvas.getContext("2d");
    this.nodes = [];
    this.relations = [];
    this.selectedNode = null;
    this.relationStart = null;

    this.resizeCanvas();
    if (typeof window !== "undefined") {
      window.addEventListener("resize", () => this.resizeCanvas());
    }

    this.canvas.addEventListener("mousedown", (e) => this.onMouseDown(e));
    this.canvas.addEventListener("mousemove", (e) => this.onMouseMove(e));
    this.canvas.addEventListener("mouseup", (e) => this.onMouseUp(e));
    this.canvas.addEventListener("dblclick", (e) => this.onDoubleClick(e));

    this.isDragging = false;
    this.dragStartX = 0;
    this.dragStartY = 0;
    this.isAddingRelation = false;
    this.relationStartNode = null;

    this.nextNodePosition = { x: 50, y: 100 }; // Starting position for nodes
    this.nodeGap = 30; // Gap between nodes

    this.lastClickTime = 0;
    this.clickDelay = 300; // milliseconds

    this.canvas.style.backgroundColor = "#ecf0f1"; // Light gray background

    console.log("DesignTool initialized");

    // Style the buttons
    this.styleButtons();

    this.projectTitle = this.getProjectTitle();

    // Initialize modal and event listeners
    this.createSOLIDModal();

    this.umlGenerator = new UMLGenerator(this);

    this.umlModal = null; // Initialize as null
  }

  createSOLIDModal() {
    // Remove existing modal if present
    const existingModal = document.getElementById("solidModal");
    if (existingModal) {
      existingModal.remove();
    }

    const modal = document.createElement("div");
    modal.id = "solidModal";
    modal.style.display = "none";
    modal.style.position = "fixed";
    modal.style.zIndex = "1000";
    modal.style.left = "0";
    modal.style.top = "0";
    modal.style.width = "100%";
    modal.style.height = "100%";
    modal.style.backgroundColor = "rgba(0,0,0,0.4)";

    const content = document.createElement("div");
    content.style.backgroundColor = "#ecf0f1";
    content.style.margin = "15% auto";
    content.style.padding = "0";
    content.style.border = "2px solid #3498db";
    content.style.borderRadius = "8px";
    content.style.width = "80%";
    content.style.maxWidth = "400px";
    content.style.boxShadow = "0 4px 8px rgba(0,0,0,0.1)";

    const header = document.createElement("div");
    header.style.backgroundColor = "#3498db";
    header.style.color = "white";
    header.style.padding = "10px 20px";
    header.style.display = "flex";
    header.style.justifyContent = "space-between";
    header.style.alignItems = "center";
    header.style.borderTopLeftRadius = "6px";
    header.style.borderTopRightRadius = "6px";

    const title = document.createElement("h2");
    title.textContent = "SOLID Principles";
    title.style.margin = "0";
    title.style.fontSize = "18px";
    title.style.fontWeight = "bold";

    const closeBtn = document.createElement("button");
    closeBtn.textContent = "×";
    closeBtn.style.color = "white";
    closeBtn.style.fontSize = "24px";
    closeBtn.style.fontWeight = "bold";
    closeBtn.style.border = "none";
    closeBtn.style.background = "none";
    closeBtn.style.cursor = "pointer";

    header.appendChild(title);
    header.appendChild(closeBtn);

    const list = document.createElement("ul");
    list.style.listStyleType = "none";
    list.style.padding = "20px";
    list.style.margin = "0";

    const principles = [
      "S: Single Responsibility Principle",
      "O: Open-Closed Principle",
      "L: Liskov Substitution Principle",
      "I: Interface Segregation Principle",
      "D: Dependency Inversion Principle",
    ];

    principles.forEach((principle) => {
      const item = document.createElement("li");
      item.style.marginBottom = "10px";
      item.style.fontSize = "14px";
      item.style.color = "#2c3e50";

      const [letter, ...rest] = principle.split(":");
      const strong = document.createElement("strong");
      strong.textContent = letter + ":";
      strong.style.color = "#3498db";
      strong.style.marginRight = "5px";

      item.appendChild(strong);
      item.appendChild(document.createTextNode(rest.join(":")));
      list.appendChild(item);
    });

    content.appendChild(header);
    content.appendChild(list);
    modal.appendChild(content);

    document.body.appendChild(modal);

    this.modal = modal;
    this.closeBtn = closeBtn;

    if (closeBtn && modal) {
      closeBtn.addEventListener("click", () => {
        modal.style.display = "none";
      });
    }

    if (modal) {
      window.addEventListener("click", (event) => {
        if (event.target == modal) {
          modal.style.display = "none";
        }
      });
    }

    const toggleSOLIDBtn = document.getElementById("toggleSOLIDBtn");
    if (toggleSOLIDBtn) {
      // Remove existing event listeners
      toggleSOLIDBtn.replaceWith(toggleSOLIDBtn.cloneNode(true));
      // Add new event listener
      document
        .getElementById("toggleSOLIDBtn")
        .addEventListener("click", () => {
          this.modal.style.display = "block";
        });
    }
  }

  getProjectTitle() {
    return prompt("Please enter your project title:", "My UML Project");
  }

  styleButtons() {
    const buttonStyle = `
      padding: 10px 15px;
      font-size: 14px;
      font-weight: 600;
      border: none;
      border-radius: 5px;
      cursor: pointer;
      transition: background-color 0.3s;
      margin: 5px;
    `;

    const addClassBtn = document.getElementById("addClassBtn");
    addClassBtn.style.cssText =
      buttonStyle + "background-color: #2ecc71; color: white;";
    addClassBtn.addEventListener("mouseover", () => {
      addClassBtn.style.backgroundColor = "#27ae60";
    });
    addClassBtn.addEventListener("mouseout", () => {
      addClassBtn.style.backgroundColor = "#2ecc71";
    });

    const addInterfaceBtn = document.getElementById("addInterfaceBtn");
    addInterfaceBtn.style.cssText =
      buttonStyle + "background-color: #e67e22; color: white;";
    addInterfaceBtn.addEventListener("mouseover", () => {
      addInterfaceBtn.style.backgroundColor = "#d35400";
    });
    addInterfaceBtn.addEventListener("mouseout", () => {
      addInterfaceBtn.style.backgroundColor = "#e67e22";
    });

    const addRelationBtn = document.getElementById("addRelationBtn");
    addRelationBtn.style.cssText =
      buttonStyle + "background-color: #e74c3c; color: white;";
    addRelationBtn.addEventListener("mouseover", () => {
      addRelationBtn.style.backgroundColor = "#c0392b";
    });
    addRelationBtn.addEventListener("mouseout", () => {
      addRelationBtn.style.backgroundColor = "#e74c3c";
    });

    const toggleSOLIDBtn = document.getElementById("toggleSOLIDBtn");
    toggleSOLIDBtn.style.cssText =
      buttonStyle + "background-color: #3498db; color: white;";
    toggleSOLIDBtn.addEventListener("mouseover", () => {
      toggleSOLIDBtn.style.backgroundColor = "#2980b9";
    });
    toggleSOLIDBtn.addEventListener("mouseout", () => {
      toggleSOLIDBtn.style.backgroundColor = "#3498db";
    });

    const generateUMLBtn = document.getElementById("generateUMLBtn");
    generateUMLBtn.style.cssText =
      buttonStyle + "background-color: #3498db; color: white;";
    generateUMLBtn.addEventListener("mouseover", () => {
      generateUMLBtn.style.backgroundColor = "#2980b9";
    });
    generateUMLBtn.addEventListener("mouseout", () => {
      generateUMLBtn.style.backgroundColor = "#3498db";
    });
    generateUMLBtn.addEventListener("click", () => {
      this.showUML();
    });
  }

  resizeCanvas() {
    if (typeof window !== "undefined") {
      this.canvas.width = window.innerWidth;
      this.canvas.height = window.innerHeight;
    } else {
      this.canvas.width = 1024;
      this.canvas.height = 768;
    }
    console.log(
      `Canvas resized to: ${this.canvas.width}x${this.canvas.height}`
    );
    this.draw();
  }

  findNextPosition() {
    const position = { ...this.nextNodePosition };

    // Update the next position
    this.nextNodePosition.x += 220 + this.nodeGap; // 200 (node width) + 30 (gap)

    // If we're near the right edge, move to the next row
    if (this.nextNodePosition.x + 200 > this.canvas.width) {
      this.nextNodePosition.x = 50;
      this.nextNodePosition.y += 120 + this.nodeGap; // 100 (node height) + 30 (gap)
    }

    // If we're near the bottom edge, reset to top
    if (this.nextNodePosition.y + 100 > this.canvas.height) {
      this.nextNodePosition = { x: 50, y: 50 };
    }

    return position;
  }

  addClass(name = "ClassName") {
    const { x, y } = this.findNextPosition();
    const node = new ClassNode(x, y, name);
    this.nodes.push(node);
    this.draw();
    console.log(`Added class ${name} at position (${x}, ${y})`);
    this.updateLayout();
  }

  addInterface(name = "InterfaceName") {
    const { x, y } = this.findNextPosition();
    const node = new InterfaceNode(x, y, name);
    this.nodes.push(node);
    this.draw();
    console.log(`Added interface ${name} at position (${x}, ${y})`);
    this.updateLayout();
  }

  startAddingRelation() {
    this.isAddingRelation = true;
    this.relationStartNode = null;
    console.log("Start adding relation mode");
  }

  addRelation(startNode, endNode, type) {
    const relation = new Relation(startNode, endNode, type);
    this.relations.push(relation);
    this.draw();
  }

  deleteNode(node) {
    this.nodes = this.nodes.filter((n) => n !== node);
    this.relations = this.relations.filter(
      (relation) => relation.startNode !== node && relation.endNode !== node
    );
    this.draw();
    console.log(`Node ${node.name} deleted`);
    this.updateLayout();
  }

  deleteRelation(relation) {
    this.relations = this.relations.filter((rel) => rel !== relation);
    this.draw();
    console.log(`Relation ${relation.type} deleted`);
  }

  editNode(node, newName, attributes = [], methods = []) {
    node.editName(newName);
    node.attributes = attributes;
    node.methods = methods;
    this.draw();
    console.log(`Node ${node.name} edited: new name - ${newName}`);
  }

  findNodeById(id) {
    return this.nodes.find((node) => node.id === id);
  }

  findRelation(startNode, endNode) {
    return this.relations.find(
      (relation) =>
        relation.startNode === startNode && relation.endNode === endNode
    );
  }

  onMouseDown(e) {
    const rect = this.canvas.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;

    if (this.isAddingRelation) {
      const clickedNode = this.nodes.find((node) => node.isPointInside(x, y));
      if (clickedNode) {
        if (!this.relationStartNode) {
          this.relationStartNode = clickedNode;
        } else {
          this.promptRelationType(this.relationStartNode, clickedNode);
          this.isAddingRelation = false;
          this.relationStartNode = null;
        }
      }
      return;
    }

    for (let node of this.nodes) {
      if (node.isDeleteButtonClicked(x, y)) {
        this.deleteNode(node);
        this.draw();
        return;
      }
      if (node.isAddPropertyButtonClicked(x, y)) {
        this.addPropertyToNode(node);
        this.draw();
        return;
      }
      if (node.isAddMethodButtonClicked(x, y)) {
        this.addMethodToNode(node);
        this.draw();
        return;
      }

      const deleteInfo = node.checkDeleteButtons(x, y);
      if (deleteInfo) {
        if (deleteInfo.type === "property") {
          node.properties.splice(deleteInfo.index, 1);
        } else if (deleteInfo.type === "method") {
          node.methods.splice(deleteInfo.index, 1);
        }
        this.draw();
        return;
      }
    }

    this.selectedNode = this.nodes.find((node) => node.isPointInside(x, y));

    if (this.selectedNode) {
      this.isDragging = true;
      this.dragOffsetX = x - this.selectedNode.x;
      this.dragOffsetY = y - this.selectedNode.y;
    }
  }

  onMouseMove(e) {
    if (this.isDragging && this.selectedNode) {
      const rect = this.canvas.getBoundingClientRect();
      const x = e.clientX - rect.left;
      const y = e.clientY - rect.top;

      this.selectedNode.x = x - this.dragOffsetX;
      this.selectedNode.y = y - this.dragOffsetY;
      console.log(
        `Node ${this.selectedNode.x} moved to (${this.selectedNode.x}, ${this.selectedNode.y})`
      );

      this.draw();
    }
  }

  onMouseUp(e) {
    this.isDragging = false;
    this.selectedNode = null;
  }

  onDoubleClick(e) {
    const rect = this.canvas.getBoundingClientRect();
    const x = e.clientX - rect.left;
    const y = e.clientY - rect.top;

    for (let node of this.nodes) {
      if (node.isPointInside(x, y)) {
        const newName = prompt("Enter new name for the node:", node.name);
        if (newName !== null && newName !== "") {
          if (typeof node.editName === "function") {
            node.editName(newName);
          } else {
            node.name = newName; // Fallback if editName doesn't exist
          }
          this.draw();
          console.log(`Node name changed to: ${newName}`);
        }
        return; // Exit the loop after handling the double-click
      }
    }
  }

  addPropertyToNode(node) {
    const property = prompt("Enter new property:");
    if (property) {
      node.addProperty(property);
      this.draw();
      this.updateLayout();
    }
  }

  addMethodToNode(node) {
    const method = prompt("Enter new method:");
    if (method) {
      node.addMethod(method);
      this.draw();
      this.updateLayout();
    }
  }

  draw() {
    this.ctx.clearRect(0, 0, this.canvas.width, this.canvas.height);

    // Draw a gradient background
    const gradient = this.ctx.createLinearGradient(0, 0, 0, this.canvas.height);
    gradient.addColorStop(0, "#ecf0f1");
    gradient.addColorStop(1, "#bdc3c7");
    this.ctx.fillStyle = gradient;
    this.ctx.fillRect(0, 0, this.canvas.width, this.canvas.height);

    console.log("Drawing nodes and relations");

    // Draw relations first so they appear behind the nodes
    this.relations.forEach((relation) => relation.draw(this.ctx));

    // Then draw nodes
    this.nodes.forEach((node) => {
      console.log(`Drawing node ${node.name}`);
      node.draw(this.ctx);
    });

    // Project title
    this.ctx.fillStyle = "#2c3e50";
    this.ctx.font = "bold 24px Arial";
    this.ctx.textAlign = "center";
    this.ctx.fillText(this.projectTitle, this.canvas.width / 2, 30);

    console.log(`Drawing project title: ${this.projectTitle}`);
  }

  generateUML() {
    return this.umlGenerator.generateUML();
  }

  createUMLModal() {
    if (this.umlModal) return; // If already created, do nothing

    const modal = document.createElement("div");
    modal.id = "umlModal";
    Object.assign(modal.style, {
      display: "none",
      position: "fixed",
      zIndex: "1000",
      left: "0",
      top: "0",
      width: "100%",
      height: "100%",
      overflow: "auto",
      backgroundColor: "rgba(0,0,0,0.4)",
      display: "flex",
      justifyContent: "center",
      alignItems: "center",
    });

    const content = document.createElement("div");
    Object.assign(content.style, {
      backgroundColor: "#fefefe",
      margin: "5% auto",
      padding: "20px",
      border: "1px solid #888",
      width: "80%",
      maxWidth: "800px",
      borderRadius: "8px",
      boxShadow: "0 4px 8px rgba(0,0,0,0.1)",
      maxHeight: "90vh",
      overflowY: "auto",
    });

    const header = document.createElement("div");
    Object.assign(header.style, {
      backgroundColor: "#3498db",
      color: "white",
      padding: "10px 20px",
      display: "flex",
      justifyContent: "space-between",
      alignItems: "center",
      borderTopLeftRadius: "8px",
      borderTopRightRadius: "8px",
    });

    const title = document.createElement("h2");
    title.textContent = "Generated UML";
    Object.assign(title.style, {
      margin: "0",
      fontSize: "18px",
      fontWeight: "bold",
    });

    const closeBtn = document.createElement("span");
    closeBtn.textContent = "×";
    Object.assign(closeBtn.style, {
      color: "white",
      fontSize: "24px",
      fontWeight: "bold",
      cursor: "pointer",
    });

    const umlContent = document.createElement("pre");
    umlContent.id = "umlContent";
    Object.assign(umlContent.style, {
      marginTop: "20px",
      whiteSpace: "pre-wrap",
      wordWrap: "break-word",
      fontFamily: "monospace",
      fontSize: "14px",
      lineHeight: "1.6",
      backgroundColor: "#f8f8f8",
      padding: "15px",
      borderRadius: "5px",
      border: "1px solid #ddd",
      maxHeight: "calc(90vh - 100px)",
      overflowY: "auto",
    });

    header.appendChild(title);
    header.appendChild(closeBtn);
    content.appendChild(header);
    content.appendChild(umlContent);
    modal.appendChild(content);

    document.body.appendChild(modal);

    closeBtn.onclick = () => {
      modal.style.display = "none";
    };

    window.onclick = (event) => {
      if (event.target == modal) {
        modal.style.display = "none";
      }
    };

    this.umlModal = modal;
    this.umlContent = umlContent;
    this.closeBtn = closeBtn;
  }

  showUML() {
    const uml = this.generateUML();
    if (uml.trim() === "") {
      console.log("No UML content to display");
      return;
    }

    if (!this.umlModal) {
      this.createUMLModal(); // Create modal only when needed
    }

    const content = document.getElementById("umlContent");
    if (content) {
      // Replace ** with <strong> tags for bold text
      content.innerHTML = uml.replace(/\*\*(.*?)\*\*/g, "<strong>$1</strong>");
      this.umlModal.style.display = "block";
    } else {
      console.error("UML content element not found");
    }
  }

  updateLayout() {
    const padding = 20;
    let currentX = padding;
    let currentY = padding + 50; // Start below the title
    let maxHeight = 0;

    this.nodes.forEach((node) => {
      if (currentX + node.width > this.canvas.width - padding) {
        currentX = padding;
        currentY += maxHeight + padding;
        maxHeight = 0;
      }

      node.x = currentX;
      node.y = currentY;

      currentX += node.width + padding;
      maxHeight = Math.max(maxHeight, node.height);
    });

    this.draw();
  }

  onAddRelationClick() {
    this.startAddingRelation();
    alert("Click on two nodes to create a relation");
  }

  promptRelationType(startNode, endNode) {
    const relationTypes = [
      "extends",
      "implements",
      "uses",
      "has",
      "associates",
    ];
    const type = prompt(
      `Enter the relation type or choose from: ${relationTypes.join(", ")}`,
      relationTypes[0]
    );
    if (type) {
      this.addRelation(startNode, endNode, type);
    }
  }
}

export { ClassNode, InterfaceNode, Relation, DesignTool };
